.contact-wrapper {
  display: flex;
  flex-direction: row;
  /* border: 1px solid #ff0000; */
  margin-top: 25vh;
  padding: 10px;
  justify-content: space-around;
  font-family: "Baloo Chettan 2", cursive;
}

.text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* border: 1px solid green; */
  width: 20vw;
}

.detail-head,
.contacts {
  display: flex;
  flex-direction: column;
}

.contact-title {
  font-weight: 900;
  font-size: calc(30px + 0.8vw);
  color: white;
}

.detail {
  font-size: calc(28px - 0.4vw);
  padding-top: 32px;
  padding-left: 5px;
}

.number,
.email,
.address {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6);
  transition: 0.3s;
  border-radius: 5px;
  padding: 16px;
  font-size: calc(10px + 0.5vw);
  margin-bottom: 2vh;
}

.email {
  font-size: calc(10px + 0.4vw);
}

.number:hover,
.email:hover,
.address:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.9);
}

.fa-phone,
.fa-envelope,
.fa-map-marker {
  padding-right: 2vmin;
  padding-left: 1vmin;
  color: #0270d7;
}

.fab.h {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6);
  transition: 0.3s;

  border-radius: 50%;

  /* width: 100%; */
  /* text-align: center; */

  display: inline-block;
  padding: 16px;
  font-size: calc(10px + 0.5vw);
  /* margin-right: 3vw; */
}

.fab.h:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.9);
  color: #0270d7;
}

.handles {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

#contactus {
  display: flex;
  flex-direction: column;
  padding: 16px;
  min-width: 25vw;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6);
  transition: 0.3s;
  border-radius: 5px; /* 5px rounded corners */
  box-sizing: border-box;
  border-radius: 5px;
}

#contactus input {
  margin-bottom: 16px;
  border-radius: 5px;
}

#message {
  padding: 16px;
  border-radius: 5px;
  border: none;
  color: white;
  background-color: rgba(200, 200, 200, 0.1);
}

.btn-msg {
  width: 10vw;
  align-self: flex-end;
  margin-top: 16px;
  border-radius: 5px;
}

@media (max-width: 640px) {
  .contact-wrapper {
    display: flex;
    flex-direction: column;
    /* border: 1px solid #ff0000; */
    margin-top: 25vh;
    padding: 10px;
    justify-content: space-around;
    font-family: "Baloo Chettan 2", cursive;
    padding: 8vw;
  }

  .text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* border: 1px solid green; */
    width: auto;
  }

  .detail {
    font-size: 5vw;
    padding-top: 32px;
    padding-left: 5px;
    padding-bottom: 5vh;
  }
  .contacts {
    margin-bottom: 4vh;
  }
  .btn-msg {
    width: auto;
    align-self: center;
    margin-top: 16px;
    border-radius: 5px;
  }
}

::selection {
  background: #2d2f36;
}
::-webkit-selection {
  background: #2d2f36;
}
::-moz-selection {
  background: #2d2f36;
}
.page-login {
  background-color: #1c2027;
  display: flex;
  flex-direction: column;
  height: 100vh;
  place-content: center;
  width: 100vw;
}
@media (max-width: 767px) {
  .page-login {
    height: auto;
    margin-bottom: 20px;
    padding-bottom: 20px;
  }
}
.container-login {
  display: flex;
  height: 320px;
  margin: 0 auto;
  width: 640px;
}
@media (max-width: 767px) {
  .container-login {
    flex-direction: column;
    height: 630px;
    width: 320px;
  }
}
.left-login {
  background-color: #323946ee;
  height: calc(100% - 40px);
  top: 20px;
  color: white;
  font-family: "Baloo Chettan 2", cursive;
  position: relative;
  width: 50%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6);
  transition: 0.3s;
  border-radius: 5px;
}
@media (max-width: 767px) {
  .left-login {
    height: 100%;
    left: 20px;
    width: calc(100% - 40px);
    max-height: 270px;
  }
}
.login-login {
  font-size: 50px;
  font-weight: 900;
  margin: 50px 40px 40px;
}
.eula-login {
  /* color: #999; */
  font-size: 14px;
  line-height: 1.5;
  margin: 40px;
}
.right-login {
  background-color: #1c2027;
  box-shadow: 0px 0px 40px 16px rgba(0, 0, 0, 0.22);
  color: #f1f1f2;
  position: relative;
  width: 50%;
}
@media (max-width: 767px) {
  .right-login {
    flex-shrink: 0;
    height: 100%;
    width: 100%;
    max-height: 350px;
  }
}
.svg-login {
  position: absolute;
  width: 320px;
}
.path-login {
  fill: none;
  stroke: url(#linearGradient-login);
  stroke-width: 4;
  stroke-dasharray: 240 1386;
}
.form-login {
  margin: 40px;
  position: absolute;
}
.label-login {
  color: #c2c2c5;
  display: block;
  font-size: 14px;
  height: 16px;
  margin-top: 20px;
  margin-bottom: 5px;
}
.input-login {
  background: transparent;
  border: 0;
  color: #f2f2f2;
  font-size: 20px;
  height: 30px;
  line-height: 30px;
  outline: none !important;
  width: 100%;
}
.input-login::-moz-focus-inner {
  border: 0;
}
#submit-login {
  color: #707075;
  margin-top: 40px;
  transition: color 300ms;
}
#submit-login:focus {
  color: #f2f2f2;
}
#submit-login:active {
  color: #d0d0d2;
}

::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}

